<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }

  .order-id:hover {
    color: #409EFF;
  }

</style>

<template>
  <div>
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="showUploadHandel" size="mini" icon="el-icon-upload2">通过Excel批量核销</el-button>
      </div>
      <hr class="s-slip">
      <!-- 搜索框 -->
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <!-- <store-group @StoreGroupChange="StoreGroupChange"></store-group> -->
          <region-city-store @StoreGroupChange="StoreGroupChange"></region-city-store>
          <el-form-item label="订单类型">
            <el-select class="search-input" v-model="searchContent.sfOrderType" filterable placeholder="请选择订单类型" size="small" @change="doSearchHandel(true)">
              <el-option v-for="item in sfOrderTypeOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="业务类型">
            <el-select class="search-input" v-model="searchContent.businessType" filterable placeholder="请选择业务类型"
              clearable size="small" @change="doSearchHandel(true)">
              <el-option v-for="item in businessTypeOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="配送类型">
            <el-select class="search-input" v-model="searchContent.orderType" filterable placeholder="请选择配送类型" clearable
              size="small" @change="doSearchHandel(true)">
              <el-option v-for="item in orderTypeOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="订单号">
            <el-input v-model.trim="searchContent.orderId" size="small" class="search-input" placeholder="请输入订单号"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>

          <el-form-item label="收货人手机号">
            <el-input v-model.trim="searchContent.receiverPhone" size="small" class="search-input" placeholder="收货人手机号"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
  
          <el-form-item label="下单时间">
            <date-picker-range 
              :defaultRange="searchContent.defaultRange"
              @datePickerRangeChange="datePickerRangeChange">
            </date-picker-range>
          </el-form-item>
  
          <el-form-item label="SKU编码/商品名称">
            <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="SKU编码/商品名称" size="small"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
  
          <el-form-item label="通知顺丰备货">
            <el-select class="search-input" v-model="searchContent.fullNotify" placeholder="请选择备货状态"
              size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in fullNotifyOptions">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="通知顺丰签收">
            <el-select class="search-input" v-model="searchContent.completeNotify" placeholder="请选择签收状态"
              size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in completeNotifyOptions">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item label="订单状态">
            <el-select class="search-input" v-model="searchContent.orderStatus" placeholder="请选择订单状态" clearable
              size="small" @change="doSearchHandel(true)">
              <el-option :label="item.value" :value="item.code" :key="item.code" v-for="item in orderStatusOptions">
              </el-option>
            </el-select>
          </el-form-item>
  
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button size="mini" class="sup-mini" :disabled="this.checkedList.length === 0" icon="el-icon-circle-check"
          @click="batchWriteOffHandel">批量核销</el-button>
      </div>

      <el-table ref="multipleTable" class="fixed-height" v-loading="loading" :data="tableData" empty-text="没有查询到您想要的数据"
        @select="handleSelect" @select-all="handleSelectAll" style="width:100%;" size="small">
        <el-table-column type="selection" label="全选" align="center">
        </el-table-column>
        <el-table-column min-width="80px" prop="platformStr" label="订单来源"></el-table-column>
        <el-table-column min-width="160px" prop="orderId" label="订单号">
          <template slot-scope="scope" size="small">
            <div class="order-id">
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="sfOrderTypeStr" label="订单类型" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="100px" prop="businessTypeStr" label="业务类型" :formatter="emptyFormatter">
        </el-table-column>
        <el-table-column min-width="86px" prop="orderTypeStr" label="配送类型" :formatter="emptyFormatter">
        </el-table-column>
        <el-table-column min-width="110px" prop="storeName" label="所属门店"></el-table-column>
        <el-table-column min-width="120px" prop="buyerName" label="收货人"></el-table-column>
        <el-table-column min-width="120px" prop="buyerPhone" label="收货手机号"></el-table-column>
        <el-table-column min-width="90px" prop="orderPayAmount" label="订单实付"></el-table-column>
        <el-table-column min-width="110px" label="通知顺丰备货">
          <template slot-scope="scope" size="small">
            <div>
              {{scope.row.sfOrderType === 0 ? scope.row.fullNotifyStr : '--'}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" prop="completeNotifyStr" label="通知顺丰签收">
          <template slot-scope="scope" size="small">
            <div>
              {{scope.row.sfOrderType === 0 ? scope.row.completeNotifyStr : '--'}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="orderStatusStr" label="订单状态" :formatter="emptyFormatter">
        </el-table-column>
        <el-table-column min-width="170px" prop="createTime" label="下单时间" :formatter="nullTreating"></el-table-column>
        <el-table-column min-width="170px" prop="prePickUpTimeFrom" label="开始自提时间"></el-table-column>
        <el-table-column min-width="170px" prop="summarizeTime" label="结单时间"></el-table-column>
        <el-table-column fixed="right" label="操作" width="140">
          <template slot-scope="scope" size="small">
            <div>
              <el-button type="text" size="mini" @click="writeOffHandel(scope.row)" icon="el-icon-circle-check">核销</el-button>
              <el-button v-if="scope.row.sfOrderType === 0" type="text" size="mini" @click="stockingHandel(scope.row, 1)" icon="el-icon-shopping-cart-full">备货</el-button>
            </div>
            <div v-if="scope.row.sfOrderType === 0"><el-button type="text" size="mini" @click="stockingHandel(scope.row, 2)" icon="el-icon-finished">签收</el-button></div>
          </template>
        </el-table-column>
      </el-table>
      <div style="padding: 10px;">
        <p><span style="color:red; padding-right:10px;">*</span>查询到订单信息，核对无误后，可操作已自提未核销的订单后台核销；</p>
      </div>
      <!-- 分页条 -->
      <el-pagination 
        v-show="tableData.length>0" 
        class="tc mt20" 
        background 
        @size-change="changeSize"
        @current-change="changeCurrent" 
        :current-page.sync="pageNum" 
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper" 
        :total="total">
      </el-pagination>
    </div>

    <batch-orders ref="batchOrders" @doSearchHandel="doSearchHandel(true)"></batch-orders>
  </div>
</template>

<script>
  import Storage from '../../utils/localStorage'
  import config from '@/utils/config'
  import * as utils from '@/utils/utils'
  import BatchOrders from './BatchOrders'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import StoreGroupAndStore from '@/components/Common/StoreCommon/StoreGroupAndStore'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
  const orderMgtUrl = {
    // 处理单据
    dealOrder: '/plateform/a/order/checkOrder',
    // 获取单据列表
    getOrderListPage: '/plateform/a/order/getSFOrderList',
    // 通知顺丰备货、收货
    notifySFOrder: '/plateform/a/order/notifySFOrder'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes20,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          regionCode: Storage.get('regionCode') || '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: [],
          sfOrderType: null, // 订单类型
          completeNotify: null, // 通知顺丰签收
          fullNotify: null, // 通知顺丰备货
          searchValue: '', // 商品名称/ERP编码
          platformId: 6, // 订单来源
          storeNo: '', // 门店编码
          orderId: '', // 订单号
          businessType: '', // 业务类型
          orderType: '', // 配送类型
          orderStatus: '',
          defaultRange: [],
          startDate: '', // 下单开始时间
          endDate: '', // 下单结束时间
          summarizeTime: '',
          receiverPhone: '' // 收货人手机号
        },
        sfOrderTypeOptions: [
          {
            label: '全部',
            value: null
          },
          {
            label: '本来鲜商品订单',
            value: 0
          },
          {
            label: '优选自营订单',
            value: 1
          },
          {
            label: '顺丰三方平台订单',
            value: 2
          }
        ],
        completeNotifyOptions: [
          {
            label: '全部',
            value: null
          },
          {
            label: '未通知',
            value: 0
          },
          {
            label: '通知成功',
            value: 1
          },
          {
            label: '通知失败',
            value: 2
          }
        ],
        fullNotifyOptions: [
          {
            label: '全部',
            value: null
          },
          {
            label: '未通知',
            value: 0
          },
          {
            label: '通知成功',
            value: 1
          },
          {
            label: '通知失败',
            value: 2
          }
        ],
        businessTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '预售',
            value: 3
          },
          // {
          //   label: '短周期预售',
          //   value: 2
          // },
          {
            label: '及时达',
            value: 4
          },
          {
            label: '拼团',
            value: 5
          },
          {
            label: '虚拟订单',
            value: 6
          }
        ],
        orderTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '及时送',
            value: 1
          },
          {
            label: '预约送',
            value: 2
          },
          {
            label: '自提',
            value: 3
          }
        ],
        // 订单状态下拉框
        orderStatusOptions: [{
            code: '',
            value: '全部'
          },
          {
            code: 0,
            value: '支付成功'
          },
          {
            code: 3,
            value: '待配送'
          },
          {
            code: 4,
            value: '待自提'
          },
          {
            code: 5,
            value: '配送中'
          },
          {
            code: 6,
            value: '已完成'
          },
          {
            code: 7,
            value: '已取消'
          },
          {
            code: 8,
            value: '待支付'
          },
          {
            code: 10,
            value: '已退款'
          },
          {
            code: 11,
            value: '已失效'
          }
        ],
        tableData: [],
        // 多选选中的内容
        checkedList: [],

        isDisabled: false, // 是否禁用
        dialogChangeStoreVisible: false, // 修改门店弹窗
        // 订单改门店时的门店编号
        storeNo: '',
        // 修改门店的参数ID
        changeStoreOrderIds: [],

        // 修改配送公司弹窗展示
        dialogChangeOrderVisible: false,
        logisticsCompanyOptList: [],
        orderNo: '', // 订单号
        logisticsCompanyId: '', // 配送公司code 1001 门店自配送    1002 美团配送
        logisticsId: '' // 该订单，该配送公司所对应的id

      }
    },
    beforeMount() {
      let searchContentMesg = Storage.get("searchContentForOpe")
      if (this.$route.meta.keepAlive && searchContentMesg) {
        this.searchContent = searchContentMesg.searchContent

        Storage.remove('searchContentForOpe')
      }
    },
    mounted() {
      // 根据用户选择的城市获取数据
      this.doSearchHandel(true)
    },
    components: {
      'batch-orders': BatchOrders,
      "date-picker-range": DatePickerRange,
      "store-group": StoreGroupAndStore,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.searchContent.cityId = params.cityId
        this.searchContent.storeNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.currentPage = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.checkedList = []
        let startDateVal = this.searchContent.startDate ? new Date(this.searchContent.startDate.replace(/-/g, '/'))
          .getTime() : 0
        let endDateVal = this.searchContent.endDate ? new Date(this.searchContent.endDate.replace(/-/g, '/'))
          .getTime() : 0

        if (this.searchContent.startDate && this.searchContent.endDate && !isNaN(startDateVal) && !isNaN(endDateVal)) {
          if (startDateVal > endDateVal) {
            this.$message("下单结束日期不能早于开始日期")
            return
          }
        }

        this.loading = true
        if (_param === true) {
          this.pageNum = 1
        }
        this.$http.post(orderMgtUrl.getOrderListPage, {
          ...this.searchContent,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          DIFFCITY: true
        }).then(res => {
          this.loading = false
          if (res.code === 0) {
            if (res.data && res.data.result && res.data.result.length) {
              for (let item of res.data.result) {
                item.redirectUrl = `/order_mgt/detail/${item.orderId}`
              }
              this.tableData = res.data.result
              if (this.pageNum == 1) {
                this.total = res.data.total
              }
            } else {
              this.tableData = []
              this.total = 0
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if (utils.isKeyInArrayBykey(row.orderId, 'orderId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('orderId', row.orderId, this.checkedList)
          return
        } else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if (val.length > 0) {
          // 全选
          for (let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
            return item
          }, [])
        } else {
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for (let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'orderId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : []
        }
      },
      /**
       * [defauleSelectFun 设置表格中的默认选中项]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        for (let i of this.tableData) {
          for (let j of this.checkedList) {
            if (i.orderId == j.orderId) {
              rows.push(i)
            }
          }
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(function () {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },

      /**
       * [changeStoreHandel 修改门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      changeStoreHandel(_row) {
        let orderIdsArr = []
        orderIdsArr.push(_row.orderId)
        this.changeStoreOrderIds = orderIdsArr
        this.storeNo = _row.storeNo
        this.dialogChangeStoreVisible = true
      },
      /**
       * [writeOffHandel 核销]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      writeOffHandel(_row) {
        this.$confirm('您确定要将所选订单完成核销吗?', '核销确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let orderIdsArr = []
          orderIdsArr.push(_row.orderId)
          let _params = {
            opType: 4, // 操作类型 1置为过期，2修改门店 3退款 4核销 
            orderIds: orderIdsArr,
            DIFFCITY: true
          }
          utils.showLoading()
          this.$http.post(orderMgtUrl.dealOrder, _params).then(res => {
            // 清空loading
            utils.clearLoading()
            if (res.code === 0) {
              this.$message.success("核销成功")
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            // 清空loading
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })

        }).catch(() => {})
      },
      /**
       * [showDetail 查看详情]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showDetail(_row) {
        // 设置搜索条件
        let _paramMesg = {
          searchContent: this.searchContent
        }
        Storage.set("searchContentForOpe", _paramMesg)
        // 去详情页
        this.$router.push("/opera_order/ope_detail/" + _row.orderId)
      },
      /**
       * 数据处理
       */
      nullTreating(row, column, cellValue) {
        if (cellValue == null) {
          return '--'
        } else {
          return cellValue
        }
      },
      /**
       * [orderTypeFormatter 格式化订单类型]
       */
      orderTypeFormatter(row, column, cellValue) {
        if (cellValue === 1) {
          return '及时送'
        } else if (cellValue === 2) {
          return '预约送'
        } else if (cellValue === 3) {
          return '自提单'
        }
      },
      /**
       * [changeLogisticsCompany description]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      changeLogisticsCompany(_val) {
        let logisticsCompanyCode = _val
        this.logisticsCompanyId = logisticsCompanyCode
        let logisticsCompan = this.logisticsCompanyOptList.filter(item => {
          return item.logisticsCompanyId === logisticsCompanyCode
        })[0]
        this.logisticsId = logisticsCompan ? logisticsCompan.logisticsId : ''
      },
      emptyFormatter(row, column, cellValue) {
        return cellValue && cellValue != "" ? cellValue : '--'
      },
      batchWriteOffHandel() {
        this.$confirm('您确定要将所选订单批量核销吗?', '批量核销', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let orderIdsArr = this.checkedList.map(item => {
            return item.orderId
          })
          let _params = {
            opType: 4, // 操作类型 1置为过期，2修改门店 3退款 4核销 
            orderIds: orderIdsArr,
            DIFFCITY: true
          }
          utils.showLoading()
          this.$http.post(orderMgtUrl.dealOrder, _params).then(res => {
            // 清空loading
            utils.clearLoading()
            if (res.code === 0) {
              this.$message.success("批量核销成功")
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            // 清空loading
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },

      /**
       * @description: 备货/签收 操作
       */
      stockingHandel(_row, _express) {
        let tipMsg = ""
        if(_express === 1) {
          tipMsg = "您确定要执行备货操作吗?"
        }else if(_express === 2) {
          tipMsg = "您确定要执行签收操作吗?"
        }
        this.$confirm(tipMsg, '备货确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let orderIdsArr = []
          orderIdsArr.push(_row.orderId)
          let _params = {
            express: _express, // 通知顺丰类型(1:备货 2:收货)
            orderIds: orderIdsArr,
            DIFFCITY: true
          }
          utils.showLoading()
          this.$http.post(orderMgtUrl.notifySFOrder, _params).then(res => {
            // 清空loading
            utils.clearLoading()
            if (res.code === 0) {
              if(_express === 1) {
                this.$message.success("执行备货操作成功")
              }else if(_express === 2) {
                this.$message.success("执行签收操作成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            // 清空loading
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },

      showUploadHandel() {
        if(this.$refs.batchOrders) {
          let _params = {
            title: '通过Excel批量核销',
            from: 'SF'
          }
          this.$refs.batchOrders.setDefaultMsg(_params)
        }
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.startDate = result ? result[0] : ''
        this.searchContent.endDate = result ? result[1] : ''
        this.doSearchHandel(true)
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave(to, from, next) {
      from.meta.keepAlive = false;
      next();
    }

  }

</script>



